ize: 1em;\n\n}\n\n.nav ul li a:hover{\n\n    color:#878787;\n}\n\n.nav.color{\n\n    background-color: #174283;\n    border-top:2px solid #fff;\n    border-left:2px solid #fff;\n    border-right:2px solid #fff;\n}\n\n.nav.color li{ color:#fff;}\n.nav.color li:hover{ color:#878787;}\n.nav.color a{ color:#fff; }\n\n\n\n/* category */\n\n.category{\n\n    padding: 30px 50px;\n    margin:0 auto;\n\n}\n\n.category h3{\n\n    padding: 10px 0px 10px 50px;\n    font-size: 1em;\n    color:#000;\n    font-weight: 400;\n\n}\n\n.category h3 span{\n\n    color:#174283;\n    font-size: 1.2em;\n    font-weight: 800;\n    padding: 0px 20px;\n\n}\n\n.category ul{\n\n    padding: 10px 0px 50px 0px;\n    margin:0 auto;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-wrap: wrap;\n}\n\n.category ul li{\n\n    margin:10px;\n    width:180px;\n    height:70px;\n    border-radius: 30px;\n    background-color: #fff;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n}\n\n.category ul li:hover{\n\n    border: 2px solid #174283;\n    transform: scale(1.06);\n    transition: transform 400ms;\n\n}\n\n.category ul li a{\n\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    color:#174283;\n    font-size: 1.2em;\n    width: 100%;\n    height: 100%;\n\n}\n\n.category ul li a img{\n\n    height:30px;\n    margin-right:8px;\n}\n\n\n\n/* board */\n\n.board{\n\n    padding: 30px 50px;\n    margin:0 auto;\n\n}\n\n.board h3{\n\n    padding: 10px 0px 30px 50px;\n    font-size: 1em;\n    font-weight: 400;\n    color:#000;\n\n}\n\n.board h3 span{\n\n    color:#174283;\n    font-size: 1.2em;\n    font-weight: 800;\n    padding: 0px 20px;\n\n}\n\n.board ul{\n\n    width: 90%;\n    padding: 20px 0px;\n    margin:0 auto;\n    background-color: rgba(225,225,225,.5);\n    border-radius: 10px;\n\n}\n\n.board ul li{\n\n    margin:8px auto;\n    padding: 10px 5px;\n    display: flex;\n    align-items: center;\n    justify-c