import React , { useState , useEffect } from 'react';

const FinDetail = ({ title , data , moreData , onClick}) => {
    
     const [ open, setOpen ] = useState();
     const list = {
         dataList : data
     }
     useEffect(() => {

         if(!data){}
         else setOpen(true)
       
     },[list.dataList])

     return(
      <div className="detail">
         <h3>{title}</h3>
         { open && (<>
           <ul className='detail_cont'>
            <li><strong></strong><p>{list.dataList[0].prdtnm}</p></li>
            <li><strong></strong><p>{list.dataList[0].member}</p></li>
            <li><strong></strong><p>{list.dataList[0].deny}</p></li>
            <li><strong></strong><p>{list.dataList[0].spcl}</p></li>
            <li><strong></strong><p>{list.dataList[0].etct}</p></li>
            <li><strong></strong><p>{list.dataList[0].mtrt}</p></li>
            { moreData && (
               <>
            <li><strong></strong><p>{list.dataList[0].erlyfee}</p></li>
            <li><strong></strong><p>{list.dataList[0].loanlmt}</p></li> 
            </>)}
         </ul>
         <ul>
            <li><strong></strong>{list.dataList[0].banknm}</li>
            <li><strong></strong>{list.dataList[0].start}</li>
            <li><strong></strong>{list.dataList[0].end}</li>
            <li onClick={onClick}></li>
         </ul> </> )}
      </div>

     )
}

export default FinDetail;